.site-nav__toggle {
  margin: 0;
  padding: 0;
  display: flex;
  box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  font-family: inherit;
  text-transform: none;
  text-decoration: none;
  word-wrap: break-word;
  color: #ffffff;
  background-color: #7859cf;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 30px;
  position: absolute;
  right: 0;
  top: 60px;
  transform: translateY(-100%);
  width: 16px;
  height: 16px;
  background-color: #ffffff;
  border: none;
  border-radius: 0;
}
.site-nav__toggle:hover, .site-nav__toggle:focus {
  background-color: #9070ec;
}
.site-nav__toggle:focus-visible {
  outline-offset: -2px;
}
.site-nav__toggle:active {
  background-color: #593cac;
}
.site-nav__toggle:disabled {
  background-color: #bdbdbd;
}
@media (min-width: 768px) {
  .site-nav__toggle {
    display: none;
  }
}
.site-nav__toggle:hover, .site-nav__toggle:focus {
  background-color: #ffffff;
  color: #f0ebff;
}
.site-nav__toggle:focus-visible {
  outline-offset: -2px;
  outline-color: #7859cf;
}
.site-nav__toggle:active {
  background-color: #f0ebff;
}

.site-nav__toggle::before {
  content: "";
  position: absolute;
  top: 24px;
  left: 22px;
  width: 16px;
  height: 2px;
  background-color: #333333;
  box-shadow: 0 5px 0 0 #333333, 0 10px 0 0 #333333;
}

.site-nav--opened .site-nav__list {
  display: flex;
  position: absolute;
  z-index: 100;
}
@media (min-width: 768px) {
  .site-nav--opened .site-nav__list {
    position: static;
    background: none;
  }
}
.site-nav--opened .site-nav__toggle::before,
.site-nav--opened .site-nav__toggle::after {
  content: "";
  position: absolute;
  top: 30px;
  left: 20px;
  width: 21px;
  height: 1.5px;
  background-color: #333333;
}
.site-nav--opened .site-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.site-nav--opened .site-nav__toggle::after {
  transform: rotate(-45deg);
}

